<template>
	<view class="">
		<scroll-view v-if='list.length>0' scroll-y="true" class="pad-lr-30 order_scroll" @scrolltolower="more">
			<order v-for="item in list" :key='item.id' :info='item' >
				<view slot='btns' class="btn-style glost" v-if="status==10 || status==60" @click="showModal(1,item.id)">取消</view>
				<view slot='btns' class="btn-style" v-if="type==1 && status==70" @click="showModal(2,item.id)">到达目的地</view>
				<view slot='btns' class="btn-style" v-if="type==2 && status==70" @click="showModal(3,item.id)">获取码</view>
				<view slot='btns' class="btn-style glost" v-if="status==50 || status==80" @click="showModal(4,item)">删除</view>
				<view slot='btns' class="btn-style mar-l-30" v-if="type==2 && status==80" @click="value(item.id)">评价</view>
				<view slot='btns' class="btn-style mar-l-30 glost" v-if="status==60 || status==70 || status==80" @click="makeCall(item.phone)">联系TA</view>
			</order>
		</scroll-view>
		<noData v-else tip='暂无相关发布'/>
		<modal title="请您确认是否取消？" v-if="modalCannel" @change='changeModalCanel' />
		<modal title="请你确认是否删除？" v-if="modalDel" @change='changeModalDel' />
		<modal title="请输入送达码" :btnCount='1' @change='changeModalInput' tip='注意：送达码需要找乘客获取' typeInput="input" :isInput="true" v-if="modalOwner" />
		<modal :title="modalTitle" :btnCount='1' :isInput="true" v-if="modalPassager" @change='changeModalCode'/>
	</view>
</template>

<script>
	import order from '../../base/order.vue'
	import modal from '../../base/modal.vue'
	import OrderApi from '../../apis/order.js'
	import noData from '../../base/nodata.vue'
	export default {
		name: 'order_view',
		components: {
			order,
			modal,
			noData
		},
		// status://发布中-10;60待出发,进行中--70；取消--50；结束--80
		// type:1--载 ,2--寄
		props: ['status', 'type'],
		data() {
			return {
				modalDel: false,
				modalOwner: false,
				modalPassager: false,
				modalCannel: false,
				modalTitle: '',
				list: [],
				page: 1,
				total: 0,
				modalId:null
			}
		},
		mounted() {
			this.init()
		},
		methods: {
			init(list=[]) {
				let data = {
					page: this.page,
					limit: 5,
					status: this.status
				}
				let type = this.type
				OrderApi.list(data, type).then(res => {
					this.total = res.total_page
					this.list = list.concat(res.list)
				})
			},
			more(){
				this.page = this.page+1
				if(this.page<=this.total){
					this.init(this.list)
				}else{
					this.$util.showToast('没用更多了')
				}
			},
			changeModalInput(flag,val){
				if(flag == 'SURE'){
					this.arrive(this.modalId,val)
				}
			},
			changeModalCanel(flag,val){
				if(flag == 'SURE'){
					this.cancel(this.modalId)
				}else{
					this.modalCannel = false
				}
			},
			changeModalDel(flag,val){
				if(flag == 'SURE'){
					this.del(this.modalId)
				}else{
					this.modalDel = false
				}
			},
			changeModalCode(flag,val){
				this.modalPassager = false
			},
			//1:cancel;2:arrive;3:code;4:del
			showModal(type,id){
				if(type==1){
					this.modalId = id
					this.modalCannel = true
				}else if(type==4){
					let id_del = this.type == 1?id.carpool_belt_id:id.carpool_hitch_id
					this.modalId = id_del
					this.modalDel = true
				}else if(type==2){
					this.modalId = id
					this.modalOwner = true
				}else if(type==3){
					this.getCode(id)
				}
			},
			cancel(id){
				OrderApi.cancel({id},this.type,this.status).then(res=>{
					this.$util.showToast('取消成功','success')
					this.filterOrder(id)
					this.modalCannel = false
				})
			},
			del(id){
				let data = {}
				if(this.type == 1){
					data.carpool_belt_id = id
				}else{
					data.carpool_hitch_id = id
				}
				OrderApi.del(data,this.type).then(res=>{
					this.$util.showToast('删除成功','success')
					//todo数据更新
					this.modalDel = false
				})
			},
			arrive(id,driver_send_code){
				OrderApi.arrive({id,driver_send_code}).then(res=>{
					this.$util.showToast('已送达','success')
					this.filterOrder(id)
					this.modalOwner = false
				})
			},
			getCode(id){
				OrderApi.getCode({id}).then(code=>{
					this.modalPassager = true
					this.modalTitle = '您的送达码为:'+code
				})
			},
			value(id){
				uni.navigateTo({
					url:'/pages/value/index?id='+id
				})
			},
			filterOrder(id){
				this.list = this.list.filter(el=>el.id !== id)
			},
			makeCall(phone){
				this.$util.makePhone(phone)
			}
		}
	}
</script>

<style lang="less" scoped>
	.order_scroll {
		box-sizing: border-box;
		min-height: calc(100vh - 100rpx);
	}
</style>
